<template>
    <section>
        <v-container>
            <v-row dark :justify="'center'">
                <v-col
                    cols="12"
                    :class="'text-center'">
                    
                    <v-form 
                    
                    ref="form"
                    v-model="valid"
                    lazy-validation
                >
                    <v-text-field
                    dark
                    v-model="name"
                    :rules="nameRules"
                    label="نام و نام خانوادگی"
                    required
                    ></v-text-field>

                    <v-text-field
                    dark
                    v-model="phone"
                    :rules="phoneRules"
                    :counter="11"
                    label="شماره همراه"
                    required
                    ></v-text-field>

                    <v-btn
                    dark
                    :disabled="!valid"
                    color="info"
                    class="mt-3"
                    @click="submit"
                    >
                    عضویت
                    </v-btn>
                </v-form>
                </v-col>
                <v-col
                    cols="12"
                    :class="'text-center'">
                    
                    <v-form v-if="submitDone" >

                    <v-text-field
                    dark
                    v-model="code"
                    label="کد ارسالی را وارد نمایید"
                    :counter="5"
                    required
                    ></v-text-field>
                    <v-btn
                    dark
                    color="success"
                    class="mt-3"
                    @click.prevent="validateCode"
                    >
                    ارسال کد ثبت نام
                    </v-btn>
                </v-form>
                </v-col>
                
                
            </v-row>
             <v-row :justify="'center'">
                <v-col cols="12" :class="'text-center'">
                    <v-btn router :to="'/'"
                    color=""
                    class="">
                     ورود به عنوان کاربر مهمان
                    </v-btn>
                </v-col>
            </v-row>
             <v-row :justify="'center'">
                <v-col cols="12" :class="'text-center'">
                    <router-link color="white" to="/login">
                      <span class="white--text"> قبلا ثبت نام کرده اید؟ اینجا کلیک کنید</span>
                    </router-link>
                </v-col>
            </v-row>
        </v-container>
    </section>
</template>
<script>
  export default {
    data: () => ({
      valid: true,
      name: '',
      submitDone: false,
      nameRules: [
        v => !!v || 'نام و نام خانوادگی الزامی است',
        v => (v && v.length <= 20) || 'نام و نام خانوادگی بایستی کمتر از 20 کاراکتر',
      ],
      phone: '',
      phoneRules: [
        v => !!v || 'شماره همراه الزامی است',
        v => /^[0-9]*$/.test(v) || 'شماره همراه معتبر نیست',
        v => (v && v.length == 11) || 'شماره همراه بایستی 11 رقمی باشد',
      ]
    }),

    methods: {
      submit(){
        if(this.$refs.form.validate()){
          this.submitDone = true;
        }
      }
  
    },
  }
</script>